import React, { Component } from "react";
import { Container, Button, Form, Label } from "semantic-ui-react";

class RegisterForm extends Component {
  render() {
    const {
      onChangeInput,
      handleSubmit,
      lengthCheckID,
      lengthCheckPassword,
      lengthCheckPasswordCheck,
      lengthCheckNickname,
      passwordEqual,
      message,
      overlapPoint
    } = this.props;

    const handleChange = e => {
      const { value, name } = e.target;
      onChangeInput({ value, name });
    };

    return (
      <Container text>
        <Form>
          <br />
          <Form.Field>
            <label></label>
            <input
              name="id"
              placeholder=" ."
              onChange={handleChange}
            />
            {overlapPoint === "id" && (
              <Label basic color="red" pointing>
                {message}
              </Label>
            )}
            {lengthCheckID === "null" && (
              <Label basic color="red" pointing>
                4  .
              </Label>
            )}
          </Form.Field>
          <Form.Field>
            <label></label>
            <input
              type="password"
              name="password"
              placeholder=" ."
              onChange={handleChange}
            />
            {lengthCheckPassword === "null" && (
              <Label basic color="red" pointing>
                6  .
              </Label>
            )}
          </Form.Field>
          <Form.Field>
            <label> </label>
            <input
              type="password"
              name="passwordCheck"
              placeholder="  ."
              onChange={handleChange}
            />
            {lengthCheckPasswordCheck === "null" && (
              <Label basic color="red" pointing>
                6  .
              </Label>
            )}
            {passwordEqual === "notEqual" && (
              <Label basic color="red" pointing>
                  .
              </Label>
            )}
          </Form.Field>

          <Form.Field>
            <label></label>
            <input
              name="nickname"
              placeholder=" ."
              onChange={handleChange}
            />
            {overlapPoint === "nickname" && (
              <Label basic color="red" pointing>
                {message}
              </Label>
            )}
            {lengthCheckNickname === "null" && (
              <Label basic color="red" pointing>
                4  .
              </Label>
            )}
          </Form.Field>
          <br />
          <Button fluid type="submit" color="green" onClick={handleSubmit}>
             
          </Button>
        </Form>
      </Container>
    );
  }
}

export default RegisterForm;
