import React from "react";
import {
  Header,
  Dropdown,
  Container,
  Button,
  Icon,
  Segment,
  Modal,
  Input
} from "semantic-ui-react";
import { Link } from "react-router-dom";
import styles from "./Header.scss";
import classNames from "classnames";
// import storage from 'lib/storage';

const cx = classNames.bind(styles);

const HeaderComponent = ({
  match,
  nickname,
  handleUserLogout,
  onChangeInput,
  onSubmit,
  onModalClose,
  onModalOpen,
  onKeyPress,
  modalState
}) => {
  const handleChange = e => {
    const { value, name } = e.target;
    onChangeInput({ value, name });
  };

  return (
    <Segment inverted style={{ marginBottom: "1em" }}>
      <Container as="h2" textAlign="right">
        {nickname === null ? (
          <Link to="/login">
            <h2 className={cx("login_text")}> / </h2>
          </Link>
        ) : (
          <div>
            {match.path === "/chatroomlist" ? (
              <Modal
                trigger={
                  <Button basic inverted color="blue" onClick={onModalOpen}>
                    <h3 className={cx("login_text")}></h3>
                  </Button>
                }
                open={modalState}
                onClose={onModalClose}
                closeIcon
              >
                <Header
                  icon="snapchat ghost"
                  content="   ."
                />
                <Modal.Content>
                  <Input
                    autoFocus
                    fluid
                    name="title"
                    placeholder="  "
                    icon="hand point right"
                    iconPosition="left"
                    onChange={handleChange}
                    onKeyPress={onKeyPress}
                  />
                </Modal.Content>
                <Modal.Actions>
                  <Button color="red" onClick={onModalClose}>
                    <Icon name="remove" /> 
                  </Button>
                  <Button color="green" onClick={onSubmit}>
                    <Icon name="checkmark" />  
                  </Button>
                </Modal.Actions>
              </Modal>
            ) : (
              <Button basic inverted color="blue">
                <Link to="/editor">
                  <h3 className={cx("login_text")}></h3>
                </Link>
              </Button>
            )}

            <Dropdown className={cx("dropbox")} text={`${nickname}`}>
              <Dropdown.Menu>
                {/* <Link to="/login">
                  <Dropdown.Item text="" />
                </Link>
                <Dropdown.Divider /> */}
                <Dropdown.Item text="" onClick={handleUserLogout} />
              </Dropdown.Menu>
            </Dropdown>
          </div>
        )}
      </Container>

      <Header as="h1" icon textAlign="center">
        <Icon name="cloud" size="massive" />
        <br />
        <Link to={`/`}>
          <Button inverted color="olive">
             
          </Button>
        </Link>
        <Link to={`/chatroomlist`}>
          <Button inverted color="violet">
            
          </Button>
        </Link>
        <Link to={`/about`}>
          <Button inverted color="orange">
            
          </Button>
        </Link>
      </Header>
    </Segment>
  );
};
export default HeaderComponent;
